html {
    font-size: 62.5%; /* 16px * 62.5 = 10px = 1rem */
    box-sizing: border-box;
  }
body{
    font-size: 1.6rem;
    font-family: Helvatica;
    height:100vh;
    box-sizing: border-box;
    margin:0;
}
.grid-container{
    display:grid;
    grid-template-areas: 
    "header"
    "main"
    "footer";
    grid-template-columns: 1fr;
    grid-template-rows: 5rem 1fr 5rem;
    height:100vh;
}
.button {
    font-size: 1.6rem;
  }
.fullwidth {
    width: 100%;
  }
  #root {
    height: 100%;
  }

/* header */
.header{
    grid-area: header;
    background-color: #203040;
    color:#ffffff;
    display: flex; 
    justify-content: space-between;
    align-items: center;
    padding: 0.5rem;;
}

.menubtn{
    font-size: 2rem;
    background: none;
    padding:.5rem;
    border:none;
    color:white;
    cursor: pointer;
}
.menubtn:hover{
    color: aqua;
}
/* Footer */
.footer{
    grid-area:footer;
    background-color: #203040;
    color:white;
    display: flex;
    justify-content: center;
    align-items: center;
}

/* main */
.main{
    grid-area:main;
}
.brand{
    color: white;
    font-size:2.5rem;
    font-weight: bold;
    text-decoration: none;
}
 .headerlinks {
    color: #ffffff;
    text-decoration: none;
    padding:1rem;
    margin-right:1rem;
 }
 .headerlinks:hover{
     color:skyblue
 }
 .products{
     display: flex;
     justify-content: center;
     align-items: center;
     flex-wrap: wrap;
 }
 .products li{
    list-style-type:none ;
    padding: 0;
    flex:0 1 24rem;
    margin:1rem;
    height:30rem;
    border-bottom: .1rem solid #c0c0c0;
 }
 .product{
     display: flex;
     flex-direction: column;
     justify-content: space-between;
     /* align-items: center; */
     height:100%;
 }
 
 .link{
   color:blue;
    text-decoration: none;
 }
 .link:hover{
     color:aqua;
 }
 .productname{
    font-size:2rem;
    font-weight: bold;
    color:blue;
}
 .productbrand{
     font-size: 1.2rem;
     color:#808080;
 }
 .productimage{
     /* border-radius: 10%; */
     max-width: 20rem;
     max-height: 20rem;
     width:30rem;
     height:30rem;
 }
 .productprice{
     font-size: 2rem;
     font-weight: bold;
 }
 .productrating{
    font-size: 1.2rem;
    color:#808080;
     margin-bottom: 1rem;
 }
 /* Reviews */
ul.review {
    list-style-type: none;
    padding: 0;
  }
  .review li {
    margin: 1rem;
    margin-bottom: 2rem;
  }
  .review li > div {
    margin: 0.5rem;
  }
  
  /* Rating */
  .rating span {
    color: #ffb901;
    font-size: 1.8rem;
    margin: 0.1rem;
  }
  .rating span:last-child {
    color: #444444;
    font-size: 1.4rem;
  }
  a > .rating > span:last-child {
    color: #0000ff;
  }

 /* sidebar */
 .sidebar{
     position: fixed;
     transition: all 1sec;
     transform: translateX(-30rem);
     width: 30rem;
     background-color: #f0f0f0;
     height: 100%;
 }
 .sidebar.open{
     transform: translateX(0);
 }
 .sidebarclosebtn{
     border-radius: 50%;
     border: 0.1rem #000000 solid;
     outline: none;
    border:none;
    width:3rem;
    height:3rem;
    padding: 0.5rem;
    font-size: 2rem;
    cursor: pointer;
    position: absolute;
    right:.5rem;
    top:1.5rem;
    color:darkgray;
 }
 .sidebarclosebtn:hover{
    color:lightblue;
     border:none;
 }
 .sidebarlist{
     list-style-type: none;
 }

 /* product details */
 .backtoresult{
     padding: 1rem;
 }
 .details{
     display: flex;
     flex-direction: row;
     justify-content: space-between;
     align-items: flex-start;
     flex-wrap: wrap;
     padding:1rem;
 }
 .detailsimage{
     flex : 2 1 40rem;
 }
 .detailsimage img{
     max-width: 40rem;
     width: 100%;
 }
 .detailsinfo{
    flex : 1 1 20rem;
}.detailsaction{
    flex : 1 1 20rem;
    border:.1rem #808080 solid;
    border-radius: 0.5rem;
    background-color: #f8f8f8;
    padding:1rem;
}
.detailsqty{
    display: flex;
    align-items: center;
    /* font-size: 1rem; */
}
.select{
    width:4rem;
    height:2rem;
    font-size: 1.5rem;
    margin-left:1rem;
}
.detailsinfo ul, .detailsaction ul{
    list-style-type: none;
    padding: 0;
}
.detailsinfo li, .detailsaction li{
   margin-bottom: 1rem;
}
.detailsaction ul li:last-child{
display: flex;
flex-direction: column;
}
.button{
    padding: 1rem;
    border: 0.1rem solid #808080;
    cursor: pointer;
    border-radius: .5rem;
    background-color: #f0c040;
    font-size: 1.2rem;
}

.button:hover{
    border:0.1rem solid #f0c040;
}
.button.primary{
    background-color: #f0c040;
}
.button.secondary{
    background-color: #f0f0f0;
}
.textcenter{
    text-align: center;
}

/* cart */
.cart{
    display: flex;
    flex-wrap: wrap;
    margin:1rem;
    align-items: flex-start; 
}
.cartlist{
    flex: 3 1 60rem;
    justify-content: space-between;
}
.cartaction{
    flex: 1 1 20rem;
    background-color: #f0f0f0;
    border: .1rem #808080 solid;
    border-radius: .5rem ;
    padding: 1rem; 
}
.cartdetails{
    display: flex;
    justify-content: space-between;
    align-items: flex-end;
    border-bottom: .1rem #808080 solid;
    padding-bottom: 1rem;
    padding-top: 1rem;;
}
.cartlistcontainer{
    list-style-type: none;
    padding-right: .5rem;
   
}
.cartlistcontainer li{
    display: flex;
    justify-content: space-between;
    padding-bottom: 1rem;
    margin-bottom: 1rem;
  border-bottom: 0.1rem #c0c0c0 solid;
}
.cartlistcontainer li:first-child{
    display: flex;
    justify-content: space-between;
    align-items: flex-end;
    margin-right:1rem ;
    /* border-bottom: .1rem #808080 solid; */
}
.cartlistcontainer li:last-child{
    align-items: right;
}
.cartlistcontainer li img{
    max-width: 10rem;
    max-height: 10rem;
}
.cartimage{
    flex: 1 1;
    margin-right: 2rem;
}
.cartname{
    flex:8 1;
}
.cartprice{
    flex: 1 1;
    font-size: 2.5rem;
    text-align: right;
}


/* forms */
.form{
    display: flex;
    justify-content: center;
    align-items: center;
    height:100%;
}
.formcontainer{
    display: flex;
    flex-direction: column;
    width:32rem;
    padding:2rem;
    padding-top: 0;
    border: 0.1rem #c0c0c0 solid;
    border-radius: .5rem;
    list-style-type: none;
}
.formcontainer li{
    display: flex;
    flex-direction: column;
    margin-bottom: 1rem;
    margin-top: 1rem;
}
input{
    padding: 1rem;
    border:.1rem #c0c0c0 solid;
    border-radius: .5rem;
}
.registerlink{
    background-color: #f0f0f0;
    display: flex;
    justify-content: center;
    align-items:center;
}


/* Products */

.product-header {
    display: flex;
    justify-content: space-between;
    align-items: flex-start;
  }
  
  .content-margined {
    margin: 1rem;
  }
  
  .table {
    width: 100%;
  }
  th {
    text-align: left;
  }
  tbody > tr:nth-child(odd) {
    background-color: #f0f0f0;
  }
  
  /* Checkout Steps */
  
  .checkout-steps {
    display: flex;
    justify-content: space-between;
    width: 40rem;
    margin: 1rem auto;
  }
  .checkout-steps > div {
    border-top: 0.3rem #c0c0c0 solid;
    color: #c0c0c0;
    flex: 1 1;
    padding-top: 1rem;
  }
  .checkout-steps > div.active {
    border-top: 0.3rem #f08000 solid;
    color: #f08000;
  }
  
  /* Place Order */
  
  .placeorder {
    display: flex;
    flex-wrap: wrap;
    padding: 1rem;
    justify-content: space-between;
  }
  .placeorder-info {
    flex: 3 1 60rem;
  }
  .placeorder-action {
    flex: 1 1 20rem;
    border: 0.1rem #c0c0c0 solid;
    border-radius: 0.5rem;
    background-color: #fcfcfc;
    padding: 1rem;
  }
  .placeorder-info > div {
    border: 0.1rem #c0c0c0 solid;
    border-radius: 0.5rem;
    background-color: #fcfcfc;
    padding: 1rem;
    margin: 1rem;
  }
  .placeorder-info > div:first-child {
    margin-top: 0;
  }
  .placeorder-action > ul {
    padding: 0;
    list-style-type: none;
  }
  .placeorder-action > ul > li {
    display: flex;
    justify-content: space-between;
    margin-bottom: 1rem;
  }
  .placeorder-action > ul > li:last-child {
    font-size: 2rem;
    font-weight: bold;
    color: #c04000;
  }
  .placeorder-actions-payment > div {
    width: 100%;
  }
  
  /* Profile */
  
  .profile {
    display: flex;
    flex-wrap: wrap;
  }
  
  .profile-info {
    flex: 1 1 30rem;
  }
  .profile-orders {
    flex: 3 1 60rem;
  }
  
  /* admin */
  .dropdown {
    display: inline-block;
    position: relative;
  }
  .dropdown-content {
    position: absolute;
    display: none;
    right: 0;
    padding: 1rem;
    list-style-type: none;
    z-index: 1;
    background-color: #203040;
    margin: 0;
    margin-top: 0.4rem;
  }
  .dropdown:hover .dropdown-content {
    display: block;
  }
  
  /* filter */
  
  .filter {
    display: flex;
    list-style-type: none;
    padding: 0;
    margin: 1rem auto;
    max-width: 45rem;
    justify-content: space-between;
    align-items: center;
  }
  .filter input,
  .filter button,
  .filter select {
    padding: 1rem;
    border-radius: 0.5rem;
    border: 0.1rem #c0c0c0 solid;
    font-size: 1.6rem;
  }
  .categories {
    padding: 0;
    list-style-type: none;
  }
  .categories a {
    display: flex;
    padding: 1rem;
  }
  
  .categories a:hover {
    background-color: #c0c0c0;
  }



  .usersigned{
      color:white;
  }